.skills{
    align-items: center;
}
.skill-section{
    margin-top: 40px;
}
.skill-section h3{
    font-size: 38px;
    color: #fca61f;
    font-weight:600;
    text-shadow:2px 2px#fce685;
}

.skills-sec-row{
    margin-top: 20px !important;
}
.progress{
width: 70%;
height: 30px !important;
font-size: 16px !important;
margin-bottom: 19px;
}
.bg-customgreen{
    background-color: #66b400 !important;
}
.bg-custompurple{
    background-color: rgb(141, 3, 72) !important;
}
@media only screen and (max-width:575px) {
    .progress{
        width:100%;
    }
    .skills .image{
        display: flex;
        justify-content: center;
    }
}








