html,body{
    margin: 0px;
    padding: 0px;
    height: 100%;
    width: 100%;
}
body{
    background-color: #f9f6ee !important;
}
*{
    font-family:cursive;
}
.blur-purple{
    position: absolute;
    width: 22rem;
    height: 12rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    filter: blur(72px);
}
.blur-blue{
    position: absolute;
    width: 22rem;
    height: 12rem;
    border-radius: 50%;
    background: #a7e8f8;
    z-index: -9;
    filter: blur(72px);
}
.wrapper h3{
    margin-bottom: 45px;
}