.otherwork-section h3{
    font-size: 38px;
    color: #fca61f;
    font-weight:600;
    margin-top: 60px;
    text-shadow:2px 2px#fce685;
}
.nav-tabs{
    justify-content: center;
    border: none !important;
}
.tab-content{
    overflow: hidden;
}
.card{
    width: 100%;
    border: 1px solid #fca61f !important;
}
.nav-tabs .nav-link{
    background-color: #68a9e5 !important;
    box-shadow: 2px 6px 6px 2px  #d7e9fc;
    margin-left: 18px;
    color: #fff !important;
}
.nav-tabs .nav-link.active{
    color: #fca61f !important;
    background-color: #fca61f !important;
    box-shadow: 2px 6px 6px 2px  #fce685;
    color: #fff !important;
}
@media only screen and (max-width: 575px) {
    .card{
        margin-top: 42px !important;
    }
}